  },
  created() {
  window.addEventListener('resize', this.handleResize)
  this.handleResize();
},
destroyed() {
  window.removeEventListener('resize', this.handleResize)
},
methods: {
  handleResize() {
    this.window.width = window.innerWidth;
    this.window.height = window.innerHeight;
  },
  wallpapper(){
    this.$refs.bg.style.backgroundImage='url("https://source.unsplash.com/'+this.window.width+'x'+this.window.height +'/?coder")';
    this.$refs.bg.style.backgroundColor='black';
    // this.$refs.bg.style.backgroundImage='url("https://source.unsplash.com/750x1334/?dark")';
    // console.log(this.$refs.bg)
  }
}
